#search-box[type="text"] {
  width: 130px;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
  background-color: transparent;
  color: #fff;
}
#search-box[type="text"]:focus {
  width: 100%;
  background-color: white;
  color: black;
}
#searchButton {
  background-color: transparent;
  color: white;
  border-width: thin;
}
#searchButton:hover {
  background-color: #5cb85c;
}
#time {
  border-color: red;
}
#weather {
  border-color: blue;
}
.clock-text #digits {
  font-size: 20vw;
}
.clock-text #period {
  font-size: 10vw;
  margin-top: 10vw;
  overflow: hidden;
}
.date-text h1 {
  font-size: 5vw;
}
.weather-text h1 {
  font-size: 5vw;
  color: gold;
}
.weather-text {
  background-color: maroon;
}
.btn-grad {
  background-image: linear-gradient(
    to right,
    #dc2424 0%,
    #4a569d 51%,
    #dc2424 100%
  );
}
.btn-grad {
  margin: 10px;
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px black;
  border-radius: 25px;
  display: block;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: large;
}

.btn-grad:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
